* {
  margin: 0;
  box-sizing: border-box;
}

.parent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  row-gap: 50px;
  padding: 20px;
}

.child1 {
  border: 1px solid rgb(255, 255, 255);
}

.child2 {
  background-color: rgb(228, 228, 228);
  width: 1000px;
  height: 400px; /* Make sure the SVG container has a fixed height */
  display: flex;
  justify-content: center; /* Center the SVG horizontally */
  align-items: center; /* Center the SVG vertically */
}

.svg_parent {
  width: 90%; /* Ensure the SVG takes full width */
  height: 100%; /* Ensure the SVG takes full height */
  overflow: visible; /* Make sure words don't get cut off */
}
